import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
import LbPicker from '../base/lb-picker'
import UniIcons from '../base/uni-icons/uni-icons'
var app = getApp().globalData
export default {
	components: {
	    LbPicker,
		UniIcons
	},
	data() {
		return {
			myProps: {
			  label: 'name',
			  value: 'name',
			  children: 'child'
			},
			triggered: false,
			tabindex:0,
			tablist:['服务商',"服务"],
			voteList:[],
			votetop:false,
			voteallowtop:true,
			votebottom:false,
			voteallowbottom:false,
			voteId:'',
			historyList:[],
			historyId:'',
			historytop:false,
			historyallowtop:true,
			historybottom:false,
			historyallowbottom:false,
			id:'',
			page1:1,
			limit:10,
			city1:'',
			search1:'',
			cate_id:'',
			List2:[],
			page2:1,
			showTitle:false,
			showTitle1:false,
			city:'全部',
			freshing:false,
			triggered1: false,
			freshing1:false,
			list:[],
			showtext:true,
		}
	},
	onLoad(e) {
		this.cate_id = e.id
		uni.showLoading({
		    title: '加载中'
		});
		// 城市
		this.getCity()
		// 服务商
		this.getVote()
		// 服务
		this.getHistory()
	},
	onShow() {
		if(uni.getStorageSync('service')){
			for(var val of this.voteList){
				if(val.id==JSON.parse(uni.getStorageSync('service')).companyId){
					val.view = JSON.parse(uni.getStorageSync('service')).view
				}
			}
			this.$nextTick(function(){
				uni.removeStorageSync('service')
			})
		}
	},
	methods: {
		company(item){
			uni.navigateTo({
				url:'/pagesTwo/MyBusiness/MyBusiness?id='+item.id
			})
		},
		chooseCity(){
			this.$refs.pic1.show()
		},
		hidep(){
			this.showtext = true
		},
		showp(){
			this.showtext = false
		},
		ok(e){
			
			if(e.value[1]=='全部'){
				this.city = e.value[1]
				this.city1 = ''
			}else{
				this.city = e.value[1]
				this.city1 = e.value[1]
			}
			// console.log(this.city1)
			this.page2 = 1
			this.page1 = 1
			// 服务商
			this.getVote()
			// 服务
			this.getHistory()
			
		},
		// 获取城市
		getCity(){
			this.$api.E_areas_list({
			}).then(res=>{
				this.list = res.data.data
				this.list.unshift({
					name: "全部",
					"child": [
						{
						   name: "全部"
						}
					]
				})
			})
		},
		serviceDetails(item){
			// console.log(item)
			uni.navigateTo({
				url:'/pagesFour/ServiceDetails/ServiceDetails?id='+item.id +'&status=0'+'&user_id='+item.user_id
			})
		},
		bottomvote(){
			if(this.voteallowbottom){
				this.votebottom = true
				this.voteallowbottom = false
				this.page1++
				setTimeout(()=> {
					this.getVote()
				}, 100);
				
			}
		},
		bottomhis(){
			if(this.historyallowbottom){
				this.historybottom = true
				this.historyallowbottom = false
				this.page2++
				setTimeout(()=> {
					this.getHistory()
				}, 100);
				
			}
		},	
		onPulling(e) {
			this.triggered = true;
		},
		onRefresh() {
			if (this.freshing) return;
			this.freshing = true;
			this.page2 = 1
			this.page1 = 1
			// this.city = '全部'
			// this.city1 = ''
			// this.search1 = ''
			this.getHistory()
			this.getVote()
			setTimeout(() => {
				this.triggered = false;
				this.freshing = false;
				uni.showToast({
				    title: '刷新成功',
				    mask: "true",
					icon:'none'
				});
			},2000)
		},
		onRestore() {
			this.triggered = false; // 需要重置
		},
		onAbort() {
			// this.triggered = false;
		},
		onPulling1(e) {
			this.triggered1 = true;
		},
		onRefresh1() {
			if (this.freshing1) return;
			this.freshing1 = true;
			this.page2 = 1
			this.page1 = 1
			// this.city = '全部'
			// this.city1 = ''
			// this.search1 = ''
			this.getHistory()
			this.getVote()
			setTimeout(() => {
				this.triggered1 = false;
				this.freshing1 = false;
				uni.showToast({
				    title: '刷新成功',
				    mask: "true",
					icon:'none'
				});
			},2000)
		},
		onRestore1() {
			this.triggered1 = false; // 需要重置
		},
		onAbort1() {
			// this.triggered1 = false;
		},
		// 详情
		InformationDetail(id){
			uni.navigateTo({
				url:'../../pagesMy/InformationDetail/InformationDetail?id='+id
			})
		},
		MyInfo(id){
			uni.navigateTo({
				url:'../../pagesMy/MyInfo/MyInfo?id='+id
			})
		},
		// 服务商列表
		getVote(){
			this.$api.sp_provider({
				page:this.page1,
				limit:20,
				city:this.city1,
				search:this.search1,
				cate_id:this.cate_id
			}).then(res=>{
				// console.log(res)
				uni.hideLoading();
				if(!this.voteallowtop){
					setTimeout(()=>{
						uni.showToast({
						    title: '刷新成功',
						    duration: 1000,
							icon:'none'
						});
						this.votetop = false
						this.voteallowtop = true
					}, 1000);
				}
				if(this.page1==1){
					if(res.data.data.length>=20){
						this.voteList = res.data.data
						this.voteallowbottom = true
						this.showTitle = false
						
					}else{
						this.voteList = res.data.data
						this.voteallowbottom = false
						this.showTitle = true
					}														
				}else{
					
					if(res.data.data.length>=20){
						this.voteList = this.voteList.concat(res.data.data)
						// console.log(this.voteList)
						this.voteallowbottom = true
						this.showTitle = false
						setTimeout(()=>{
							uni.showToast({
							    title: '加载成功',
							    duration: 1000,
								icon:'none'
							});
							this.votebottom = false
						}, 1000);
						 
					}else{
						this.voteList = this.voteList.concat(res.data.data)
						this.voteallowbottom = false
						this.showTitle = true
						setTimeout(()=>{
							uni.showToast({
							    title: '加载成功',
							    duration: 1000,
								icon:'none'
							});
							this.votebottom = false
						}, 1000);
						
					}
					
					
				}
				
				
			})
		},
		// 服务列表
		getHistory(){
			this.$api.sp_service({
				page:this.page2,
				limit:20,
				city:this.city1,
				search:this.search1,
				cate_id:this.cate_id,
				id:this.id
			}).then(res=>{
				if(!this.historyallowtop){
					setTimeout(()=>{
						uni.showToast({
						    title: '刷新成功',
						    duration: 1000,
							icon:'none'
						});
						this.historytop = false
						this.historyallowtop = true
					}, 1000);
				}
				if(this.page2 == 1){
					// console.log(1)
					if(res.data.data.length>=20){
						this.historyList = res.data.data
						this.historyallowbottom = true
						this.showTitle1 = false
						
					}else{
						this.historyList = res.data.data
						this.historyallowbottom = false
						this.showTitle1 = true
					}														
				}else{
					// console.log(2)
					if(res.data.data.length>=20){
						this.historyList = this.historyList.concat(res.data.data)
						this.showTitle1 = false
						this.historyallowbottom = true
						setTimeout(()=>{
							uni.showToast({
							    title: '加载成功',
							    duration: 1000,
								icon:'none'
							});
							this.historybottom = false
						}, 1000);
						 
					}else{
						this.historyList = this.historyList.concat(res.data.data)
						this.historyallowbottom = false
						this.showTitle1 = true
						setTimeout(()=>{
							uni.showToast({
							    title: '加载成功',
							    duration: 1000,
								icon:'none'
							});
							this.historybottom = false
						}, 1000);
						
					}
					
					
				}
			})
		},
		changeSwiper(e){
			this.tabindex = e.detail.current
		},
		tab(ind){
			this.tabindex = ind
		},
		bindPickerChange(e){
			this.city = e.detail.value[1].replace(/省|市|自治区|特别行政区|壮族|回族|维吾尔|城区/g, '')
			this.city1 = this.city
			uni.showLoading({
			    title: '加载中'
			});
			this.page1 = 1
			this.page2 = 1
			this.$nextTick(()=>{
				this.getVote()
				this.getHistory()
			})
		},
		onChange(e){
			// console.log(e)
			this.search1 = e.detail
			this.page1 = 1
			this.page2 = 1
			uni.showLoading({
			    title: '加载中'
			});
			setTimeout(()=>{
				this.getVote()
				this.getHistory()
			}, 300);

		},
		onCancel(){
			if(this.search1){
				this.search1 = ''
				this.page1 = 1
				this.page2 = 1
				uni.showLoading({
				    title: '加载中'
				});
				setTimeout(()=>{
					this.getVote()
					this.getHistory()
				}, 300);
			}
			
			
		}
	}
}
